.loginWrap {
  position: relative;
  width: 100%;
  height: 100%;
  background-image: url(/renleiabc/react-management-system/static/media/bj.534278be.png);
  background-size: 100%;
}
.msTitle {
  width: 100%;
  line-height: 50px;
  text-align: center;
  font-size: 20px;
  color: #fff;
  border-bottom: 1px solid #ddd;
}
.msLogin {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 350px;
  margin: -190px 0 0 -175px;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.3);
  overflow: hidden;
}
.msContent {
  padding: 30px 30px;
}
.loginBtn {
  text-align: center;
}
.loginBtn button {
  width: 100%;
  height: 36px;
  margin-bottom: 10px;
}
.loginTips {
  font-size: 12px;
  line-height: 30px;
  color: #fff;
}
canvas {
  display: block;
  vertical-align: bottom;
}
#aa_particles {
  z-index: 4;
  height: 100%;
  height: 100vh;
  overflow: hidden;
  /* background: #0c112d; */
  background-image: -webkit-linear-gradient(#0c112d, #000, #0c112d);
  position: relative;
}
